<template>
  <div class="box">
    <div v-show="bool">你还没有评论</div>
    <ul>
      <li v-for="(ietm, index) in history" :key="index">
           <transition  name="lianimate">
        <div v-show="ietm.plct" class="comment-box">
          <div class="comment-box-header">
            <img :src="ietm.imgurl" alt="" class="img-one" />
            <span class="text">{{ ietm.text }}</span>
            <span class="type">{{ ietm.type }}</span>
          </div>
          <img :src="headerImg" alt="" class="img-two" />

          <div class="comment-box-footer">
            <span class="name">{{ username }}</span>
            <span class="date">{{ ietm.date.slice(0, 10) }}</span>
            <span class="comment">{{ ietm.pl }}</span>
          </div>
          <div class="comment-box-tail">
            <button>分享</button><button @click="clickRemove(ietm)">删除</button>
          </div>
        </div>
        </transition>
      </li>
    </ul>
    <div style="height: 100px"></div>
  </div>
</template>
<script>
export default {
  props: ["history", "username", "headerImg"],
  data() {
    return {
      bool: true,
    };
  },
  created() {
    for (let i = 0; i < this.history.length; i++) {
      if (this.history[i].plct === true) {
        this.bool = false;
      }
    }
  },
  methods:{
      clickRemove(ietm){
          ietm.plct = false
          ietm.pl = ""

      }
  }
};
</script>
<style lang="scss" scoped>
.box {
  margin-top: 150px;
  width: 100%;
  height: 100%;
  background-color: #ccc;

  .img-one {
    width: 35px;
    height: 28px;
  }

  .img-two {
    margin-left: 5%;
    width: 40px;
    height: 40px;
    border-radius: 50%;
  }

  .comment-box {
    margin-bottom: 10px;
    background-color: #fff;
  }

  .comment-box-header {
    position: relative;
    height: 57px;
    width: 100%;
    padding: 10px 5% 0 5%;
    margin-bottom: 10px;
    border-bottom: 1px solid #aaa;

    .text {
      position: absolute;
      top: 11px;
      margin-left: 10px;
      font-size: 13px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      max-width: 243px;
    }

    .type {
      position: absolute;
      bottom: 10px;
      margin-left: 10px;
      font-size: 12px;
      color: #aaa;
    }
  }

  .comment-box-footer {
    float: right;
    height: 60px;
    width: 80%;
    border-bottom: 1px solid #aaa;

    .date {
      float: right;
      margin-right: 10px;
      font-size: 12px;
      color: #aaa;
    }
    .name {
      font-size: 20px;
      margin-left: 10px;
    }
    .comment {
      display: block;
      margin: 10px 0 0 10px;
      height: 28px;
    }
  }

  .comment-box-tail {
    // float: right;
    transform: translateX(280%);
    margin-top: 30px;
    width: 100px;
    height: 30px;

    button {
      margin: 0 2px;
      background-color: #fff;
      border: 1px solid orange;
      color: orangered;
      outline: none;
    }
  }

   .lianimate-leave-active {
        transition: all .5s linear;
    }
    .lianimate-leave-to {
        transform: translateX(-100px);
        opacity: 0;
    }
}
</style>